body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

nav {
	padding: 0 1rem;
}

.content {
	min-height: calc(100vh - 134px);
	padding: 1.5rem;
}

.list {
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
	gap: 1rem;
	grid-gap: 1rem;
}

.card {
	display: grid;
	flex-direction: column;
	position: relative;
	margin: 0.2rem;
}

.card-image {
	position: relative;
}

.card-content {
	padding: 24px;
}

.card-title {
	display: block;
	line-height: 32px;
	margin-bottom: 8px;
}

.list-config {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;

}

.card-config {
	display: flex;
	flex-direction: row;
}

.card-image-config {
	min-width: 120px;
	position: relative;
	margin: 16px;
	margin-top: 68px;
}

iframe {
	width: 600px;
	height: 350px;
}

@media(max-width:700px) {
	iframe {
		width: 480px;
	}

	.card-image-config {
		margin-top: 134px;
	}
}

@media(max-width:540px) {
	iframe {
		width: 360px;
	}

	.card-image-config {
		margin-top: 100px;
	}
}

@media(max-width:400px) {
	iframe {
		width: 260px;
	}

	.card-image-config {
		margin-top: 134px;
	}
}

@media(max-width:330px) {
	iframe {
		width: 260px;
	}

	.card-image-config {
		margin-top: 134px;
	}
	.card-content{
		padding: 0;
	}
	.card-config{
		flex-direction: column;
	}
}